"use client"

import { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Mascot } from '@/components/indiko/mascot'
import Link from 'next/link'
import { useSearchParams } from 'next/navigation'

export default function CadastroPage() {
  const searchParams = useSearchParams()
  const [activeTab, setActiveTab] = useState(searchParams?.get('mode') === 'login' ? 'login' : 'cadastro')
  const [formData, setFormData] = useState({
    nome: '',
    email: '',
    telefone: '',
    senha: ''
  })

  const handleInputChange = (field: string, value: string) => {
    setFormData(prev => ({ ...prev, [field]: value }))
  }

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    // Aqui seria a integração com o backend
    console.log('Form submitted:', formData)
    // Redirecionar para dashboard após sucesso
    window.location.href = '/dashboard'
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-white flex flex-col">
      {/* Header */}
      <div className="flex items-center justify-between p-6">
        <Link href="/" className="flex items-center space-x-2">
          <Mascot size="sm" />
          <span className="font-poppins font-bold text-xl text-gray-900">Indiko</span>
        </Link>
      </div>

      {/* Main Content */}
      <div className="flex-1 flex items-center justify-center px-6 py-8">
        <Card className="w-full max-w-md shadow-xl border-0">
          <CardHeader className="text-center pb-6">
            <div className="mb-4">
              <Mascot size="lg" animated={true} className="mx-auto" />
            </div>
            <CardTitle className="font-poppins font-bold text-2xl text-gray-900">
              {activeTab === 'cadastro' ? 'Criar Conta' : 'Entrar'}
            </CardTitle>
            <p className="text-gray-600 mt-2">
              {activeTab === 'cadastro' 
                ? 'Comece agora e ganhe 50 pontos iniciais!' 
                : 'Bem-vindo de volta ao Indiko!'
              }
            </p>
          </CardHeader>

          <CardContent>
            <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full">
              <TabsList className="grid w-full grid-cols-2 mb-6">
                <TabsTrigger value="cadastro" className="font-semibold">Cadastrar</TabsTrigger>
                <TabsTrigger value="login" className="font-semibold">Entrar</TabsTrigger>
              </TabsList>

              <TabsContent value="cadastro">
                <form onSubmit={handleSubmit} className="space-y-4">
                  <div className="space-y-2">
                    <Label htmlFor="nome" className="font-semibold">Nome completo</Label>
                    <Input
                      id="nome"
                      type="text"
                      placeholder="Seu nome completo"
                      value={formData.nome}
                      onChange={(e) => handleInputChange('nome', e.target.value)}
                      className="h-12 rounded-xl border-2 focus:border-blue-500"
                      required
                    />
                  </div>

                  <div className="space-y-2">
                    <Label htmlFor="email" className="font-semibold">E-mail</Label>
                    <Input
                      id="email"
                      type="email"
                      placeholder="seu@email.com"
                      value={formData.email}
                      onChange={(e) => handleInputChange('email', e.target.value)}
                      className="h-12 rounded-xl border-2 focus:border-blue-500"
                      required
                    />
                  </div>

                  <div className="space-y-2">
                    <Label htmlFor="telefone" className="font-semibold">WhatsApp</Label>
                    <Input
                      id="telefone"
                      type="tel"
                      placeholder="(11) 99999-9999"
                      value={formData.telefone}
                      onChange={(e) => handleInputChange('telefone', e.target.value)}
                      className="h-12 rounded-xl border-2 focus:border-blue-500"
                      required
                    />
                  </div>

                  <div className="space-y-2">
                    <Label htmlFor="senha" className="font-semibold">Senha</Label>
                    <Input
                      id="senha"
                      type="password"
                      placeholder="Mínimo 6 caracteres"
                      value={formData.senha}
                      onChange={(e) => handleInputChange('senha', e.target.value)}
                      className="h-12 rounded-xl border-2 focus:border-blue-500"
                      required
                      minLength={6}
                    />
                  </div>

                  <Button 
                    type="submit"
                    className="w-full h-12 mt-6 bg-blue-600 hover:bg-blue-700 text-white font-semibold rounded-xl shadow-lg hover:shadow-xl transition-all duration-300"
                  >
                    Criar Conta
                  </Button>
                </form>
              </TabsContent>

              <TabsContent value="login">
                <form onSubmit={handleSubmit} className="space-y-4">
                  <div className="space-y-2">
                    <Label htmlFor="email-login" className="font-semibold">E-mail</Label>
                    <Input
                      id="email-login"
                      type="email"
                      placeholder="seu@email.com"
                      value={formData.email}
                      onChange={(e) => handleInputChange('email', e.target.value)}
                      className="h-12 rounded-xl border-2 focus:border-blue-500"
                      required
                    />
                  </div>

                  <div className="space-y-2">
                    <Label htmlFor="senha-login" className="font-semibold">Senha</Label>
                    <Input
                      id="senha-login"
                      type="password"
                      placeholder="Sua senha"
                      value={formData.senha}
                      onChange={(e) => handleInputChange('senha', e.target.value)}
                      className="h-12 rounded-xl border-2 focus:border-blue-500"
                      required
                    />
                  </div>

                  <div className="text-right">
                    <Link href="#" className="text-sm text-blue-600 hover:text-blue-700 font-medium">
                      Esqueceu a senha?
                    </Link>
                  </div>

                  <Button 
                    type="submit"
                    className="w-full h-12 mt-6 bg-blue-600 hover:bg-blue-700 text-white font-semibold rounded-xl shadow-lg hover:shadow-xl transition-all duration-300"
                  >
                    Entrar
                  </Button>
                </form>
              </TabsContent>
            </Tabs>

            {/* Login Social */}
            <div className="mt-6">
              <div className="relative">
                <div className="absolute inset-0 flex items-center">
                  <div className="w-full border-t border-gray-300"></div>
                </div>
                <div className="relative flex justify-center text-sm">
                  <span className="px-2 bg-white text-gray-500">ou continue com</span>
                </div>
              </div>

              <div className="mt-4 space-y-3">
                <Button 
                  variant="outline"
                  className="w-full h-12 border-2 rounded-xl font-semibold hover:bg-gray-50"
                >
                  <div className="w-5 h-5 bg-blue-600 rounded mr-3"></div>
                  Google
                </Button>
                
                <Button 
                  variant="outline"
                  className="w-full h-12 border-2 rounded-xl font-semibold hover:bg-gray-50"
                >
                  <div className="w-5 h-5 bg-blue-500 rounded mr-3"></div>
                  Facebook
                </Button>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
